/* Blocks: Unique Classes - Global */

// Gallery: Fancybox Outline 
.is-style-fancybox {
	a {
		border: 1px solid $gray-dark;
	}
	img {
	    transition: all .5s;
	    opacity: 0.7;
	    &:hover {
		    opacity: 1;
	    }

	}
}


// Columns: Features (Plain text w/lists)
.is-style-stever-columns-features {
	.wp-block-column {
		margin: 2em 0 0 0;

		h4 {
			margin-top: 0;
		}
		// (the second column)
		& + .wp-block-column {
			border-left: 1px solid #F1f1f1;
			padding-left: 4em;
		}
	}
}
@include breakpoint( l ) {
	.is-style-stever-columns-features{
		.wp-block-column {
			padding-right: 2em;
			
			&:last-child {
				padding-left: 2em;
			}
		}
	}
}
@include breakpoint( m ) {
	.is-style-stever-columns-features .wp-block-column,
	.is-style-stever-columns-features .wp-block-column:last-child {
	    padding: 0;
	    flex-basis: 100% !important;
	    border: 0 none;
	}
}


// Columns: Statistics (Stylized backgrounds w/lists)
.is-style-stever-columns-statistics {
	.wp-block-column {
		background: #f1f1f1;
		padding: 2em;
		margin-bottom: 1em;
		color: white;
		border-top-left-radius: 30px;
		border-bottom-right-radius: 30px;
		transition: all .5s;
		
		&:hover {
			box-shadow: inset 0 0 10px 5px #CCC;
		}
		
		h4 {
			margin: 0 0 .5em 0;
			font-size: 2.5em;
			font-weight: bold;
			color: $white;
			
			&:before {
				font-family: $fonts-icon;
				display: block;
				margin-bottom: 0.5em;
				font-size: .75em;
				text-align: center;
			}
		}
		h5 {
			color: $white;
		}
	}
}


// Columns: Statistics (Stylized backgrounds w/lists) - Admin
.wp-admin .is-style-stever-columns-statistics .wp-block-column h4 {
	font-size: 1.25em;
}


// Columns: Statistics (Stylized backgrounds w/lists) - Class Variations: Icon & Color.
.is-style-stever-columns-statistics {
	// "Pointer" Icon (Green)
	.is-style-stever-columns-statistics-pointer {
		background: $accent-green;
		
		h4:before {
			content: "\f245";
		}
	}
	// "Themes" Icon (Teal)
	.is-style-stever-columns-statistics-themes {
		background: $accent-teal;
		
		h4:before {
			content: "\f0e8";
		}
	}
	// Teams Icon (Red)
	.is-style-stever-columns-statistics-team {
		background: $accent-red;
		
		h4:before {
			content: "\f0c0";
		}
	}
	// Commits Icon (Blue)
	.is-style-stever-columns-statistics-commits {
		background: $accent-blue;
		
		h4:before {
			content: "\f126";
		}
	}
}